@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
.container {
  position: relative;
  width: 1440px;
  height: 960px;
  background: #FFFFFF;
}

.mainImage {
  position: absolute;
  width: 505px;
  height: 330px;
  left: 230px;
  top: 96px;
}

.title {
  position: absolute;
  width: 380px;
  height: 50px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 25px - 319px);
  /* T1 */
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  /* or 50px */
  /* Dark #1 */
  color: #121212;
}

.description {
  position: absolute;
  width: 380px;
  height: 96px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 48px - 222px);
  /* Body Copy */
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  /* or 24px */
  /* Dark #1 */
  color: #121212;
}

.openPopupButton {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 16px 40px;
  gap: 10px;
  position: absolute;
  width: 128px;
  height: 48px;
  left: 830px;
  top: 338px;
  /* Primary #1 */
  background: #E4C1AE;
  border-radius: 32px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  /* set text color to white */
  color: #FFFFFF;
}